
@import url(~normalize.css/normalize.css);
[v-cloak] {
  display: none;
}
body {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  color: color(fjord);
  font-family: "Source Sans Pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-font-feature-settings: "kern", "liga", "pnum";
          font-feature-settings: "kern", "liga", "pnum";
  font-size: 1rem;
  line-height: 1.3;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  background: #FAFBFE;
}
h1, h2, h3, h4 {
  font-family: "Source Sans Pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #E8E9ED;
  margin: 1em 0;
  padding: 0;
}
.content {
  background: #FAFBFE;
  min-height: calc(100vh - 180px);
}
.title {
  text-align: center;
  color: #4F5566;
}
.container {
  max-width: 90rem;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
  box-sizing: border-box;
}
.loading {
  text-align: center;
  color: #4F5566;
  font-size: 1.125rem;
}
.error-message {
  text-align: center;
  color: #00C4C9;
}
.Search__container {
  max-width: 40rem;
  padding: 6.25rem 0 1.25rem 0;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.Search__input {
  padding: 1.25rem;
  background-color: #fff;
  border: 1px solid #E8E9ED;
  font-size: 1rem;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.Search__button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding: 1.125rem 2.8125rem;
  margin-left: 1.25rem;
  border: 0;
  border-radius: 0;
  cursor: pointer;
  text-align: center;
  font-size: 1.3125rem;
  font-weight: 600;
  color: #fff;
  background: #00C4C9;
}
.Search__button:hover, .Search__button:focus, .Search__button:active {
    background: #009296;
}
.Search__icon {
  width: 2.5rem;
  height: 2.5rem;
  margin-left: 0.9375rem;
  cursor: pointer;
  color: #4F5566;
}
.Search__icon:hover, .Search__icon:focus, .Search__icon:active {
    color: #00C4C9;
}
.Search__icon > svg {
    fill: currentColor;
}
.Search__settings {
  max-width: 40rem;
  margin: 0 auto;
  padding: 1.25rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.Search__settings .vdp-datepicker + .vdp-datepicker {
    margin-left: 1.25rem;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.Chart__container {
  border-radius: 0.25rem;
  background-color: #fff;
  box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.3);
  padding: 1.25rem 2.5rem;
  margin: 3.125rem 0;
}
.Chart__title {
  color: #4F5566;
  margin-bottom: 2.5rem;
  font-weight: 600;
  font-size: 1rem;
}
.Chart__title > span {
    font-weight: 400;
    color: #00C4C9;
    font-size: 1rem;
    margin-left: 1.5625rem;
}
.sk-cube-grid {
  width: 40px;
  height: 40px;
  margin: 100px auto;
}
.sk-cube-grid .sk-cube {
  width: 33%;
  height: 33%;
  background-color: #00C4C9;
  float: left;
  -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
  animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
}
.sk-cube-grid .sk-cube1 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
.sk-cube-grid .sk-cube2 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}
.sk-cube-grid .sk-cube3 {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
}
.sk-cube-grid .sk-cube4 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}
.sk-cube-grid .sk-cube5 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
.sk-cube-grid .sk-cube6 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}
.sk-cube-grid .sk-cube7 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
.sk-cube-grid .sk-cube8 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}
.sk-cube-grid .sk-cube9 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
@-webkit-keyframes sk-cubeGridScaleDelay {
0%, 70%, 100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1);
}
35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1);
}
}
@keyframes sk-cubeGridScaleDelay {
0%, 70%, 100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1);
}
35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1);
}
}



